import makeClient from './http-commons.js'

import {memo} from '@roundforest/functional-commons'
import nodeFetch from 'node-fetch'
import {Agent as HttpsAgent} from 'https'
import {Agent} from 'http'

const requestIdGetterSymbol = Symbol('request-id-getter')

/**
 * @typedef {import('node-fetch').RequestInit & {requestId?: string}} RequestInitWithRequestId
 * */

const defaultAgent = memo(
  /**@param {string} protocol*/ (protocol) =>
    protocol === 'http:' ? new Agent(SOCKET_OPTIONS) : new HttpsAgent(SOCKET_OPTIONS),
)

const HTTP_FETCH_SOCKET_TIMEOUT = parseInt(
  process.env.HTTP_FETCH_SOCKET_TIMEOUT || `${5 * 60 * 1000}`,
  10,
)

/** @type {import('http').AgentOptions} */
const SOCKET_OPTIONS = {keepAlive: true, timeout: HTTP_FETCH_SOCKET_TIMEOUT}

/**
 * @param {string | URL} url
 * @param {RequestInitWithRequestId} [init]
 * @returns {Promise<import('node-fetch').Response>}
 */
async function fetcher(url, init) {
  // @ts-expect-error
  const requestId = init?.requestId ?? globalThis[requestIdGetterSymbol]?.()

  return await nodeFetch(url.toString(), {
    //@ts-expect-error
    agent: defaultAgent(url?.protocol ?? new URL(url).protocol),
    ...(requestId != null
      ? {
          ...init,
          headers: {
            'x-request-id': requestId,
            ...init?.headers,
          },
        }
      : init),
  })
}
/** @typedef {typeof fetcher} Fetcher */

const client = makeClient(fetcher, requestIdGetterSymbol)

const {
  throwErrorFromBadStatus,
  fetchAsJson,
  fetchAsText,
  fetchAsTextWithJsonBody,
  fetchAsBuffer,
  fetchAsBufferWithJsonBody,
  fetchAsJsonWithJsonBody,
  setGlobalRequestIdGetter,
  retryFetch,
  fetch,
} = client

export {
  throwErrorFromBadStatus,
  fetchAsJson,
  fetchAsText,
  fetchAsTextWithJsonBody,
  fetchAsBuffer,
  fetchAsBufferWithJsonBody,
  fetchAsJsonWithJsonBody,
  setGlobalRequestIdGetter,
  retryFetch,
  fetch,
  requestIdGetterSymbol,
}
