import camelcaseKeys from 'camelcase-keys'

import {slugToText} from '@roundforest/text-transforms-commons'

import {
  makeClient as realClient,
  getManualDealsByPositionOrUndefined,
  type CommonBdtParameters,
  RegisterEmailData,
  SubscriptionOriginalApiDataModel,
  normalizePageTypeCategories,
} from './bdt-client.js'
import {
  ProductsAndMetadataResponse,
  PageTypeResponse,
  ManualDealsResponse,
  ManualDealsParams,
  CategoryOriginalApiModel,
  TrackRequestApiResponse,
} from './models/models.js'
import {mockData as productsAndMetadataMock} from './fixtures/products-and-metadata.db.js'
import {getPageTypeDataMock} from './fixtures/page-type.db.js'
import {getManualDealsMock} from './fixtures/manual-deals.db.js'
import {categoriesMock} from './fixtures/categories.db.js'

export * from './fixtures/categories.db.js'

export function makeClient(): ReturnType<typeof realClient> {
  return {
    getProductsAndMetadata,
    submitContactForm,
    getPageTypeData,
    getManualDeals,
    trackRequest,
    registerEmail,
    getAllCategories,
    TEST_clear: () => {},
  }

  async function submitContactForm() {
    return undefined
  }

  async function registerEmail(data: RegisterEmailData): Promise<{type: string}> {
    const dataForApi: SubscriptionOriginalApiDataModel = {
      email: data.email,
      app_id: data.appId,
      user_id: data.userId,
      subscription_location: data.subscriptionLocation,
    }
    if (dataForApi.email === 'test@test.com') {
      return {
        type: '0',
      }
    }
    if (dataForApi.email === 'test1@test.com') {
      return {
        type: '1',
      }
    }
    if (dataForApi.email === 'test2@test.com') {
      return {
        type: '2',
      }
    }
    throw Error
  }

  async function getProductsAndMetadata(): Promise<ProductsAndMetadataResponse> {
    const {products, metadata} = productsAndMetadataMock

    // convert all keys to camelCase
    const normalizedProducts = camelcaseKeys(products)
    const normalizedMetadata = camelcaseKeys(metadata)

    return {
      products: normalizedProducts.map((product) => ({...product, clickId: 123456})),
      metadata: normalizedMetadata,
    }
  }

  async function getPageTypeData(
    _commonBdtParameters: CommonBdtParameters,
    searchTerm: string,
  ): Promise<PageTypeResponse> {
    const slugAsText = slugToText(searchTerm, {capitalize: false})

    const {page_type, popular_categories, subcategories, banner} = await getPageTypeDataMock(
      slugAsText as any,
    )

    const popularCategories = normalizePageTypeCategories(popular_categories || [])
    const normalizedSubcategories = normalizePageTypeCategories(subcategories || [])

    return {
      banner,
      pageType: page_type,
      popularCategories,
      subcategories: normalizedSubcategories,
    }
  }

  async function getManualDeals({positions}: ManualDealsParams): Promise<ManualDealsResponse> {
    const response = getManualDealsMock(positions)

    return {
      topDeals: getManualDealsByPositionOrUndefined(positions, 'Top Deals', response),
      topOffers: getManualDealsByPositionOrUndefined(positions, 'Top Offers', response),
      popups: getManualDealsByPositionOrUndefined(positions, 'Popups', response),
      banners: getManualDealsByPositionOrUndefined(positions, 'Banners', response),
      coupons: getManualDealsByPositionOrUndefined(positions, 'Coupons', response),
      offers: getManualDealsByPositionOrUndefined(positions, 'Offers', response),
    }
  }
  async function trackRequest(
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    _commonBdtParameters: CommonBdtParameters,
  ): Promise<TrackRequestApiResponse> {
    return {
      traffic: {type: 'direct'},
      trackRequestCookies: {},
    }
  }

  async function getAllCategories(): Promise<CategoryOriginalApiModel[]> {
    return categoriesMock
  }
}
