import camelcaseKeys from 'camelcase-keys'

import {
  fetchAsJsonWithJsonBody,
  fetchAsJson,
  fetch,
  throwErrorFromBadStatus,
} from '@roundforest/http-commons'
import {buildUrl} from '@roundforest/url-commons'
import {slugToText} from '@roundforest/text-transforms-commons'
import {mapHostHeaderToLocaleParams} from '@roundforest/bdt-locale-commons'

import {
  ContactFormData,
  ProductsAndMetadataResponse,
  PageTypeResponse,
  PageTypeOriginalApiResponse,
  ManualDealsParams,
  ManualDealsOriginalApiResponse,
  ManualDealsResponse,
  ManualDeal,
  ManualDealOriginalApiModel,
  ManualDealsPositions,
  RegisterEmailData,
  SubscriptionOriginalApiDataModel,
  ProductsAndMetadataOriginalApiResponse,
  Category,
  CategoryOriginalApiModel,
  TrackRequestApiResponse,
} from './models/models.js'
import {randomNumber} from './commons/random-number.js'
import {modifyBaseUrlBasedOnHostHeader} from './modify-base-url-based-on-host-header.js'

export * from './models/models.js'

export type CommonBdtParameters = {
  userAgent?: string | undefined
  userId?: string | undefined
  sessionId?: string | undefined
  pageId?: string | undefined
  requestPath?: string | undefined
  traffic?: string | undefined
  bdtSession?: string | undefined
  hostHeader?: string | undefined
  abTests?: Record<string, any> | undefined
}

export type CookieSetterFunction = (key: string, value: string) => void

export type TrackingCookies = {
  traffic?: string | undefined
  bdtSession?: string | undefined
}

function extractTrafficFromCookieValue(
  rawTrafficString: string | undefined,
): TrackRequestApiResponse['traffic'] {
  if (!rawTrafficString) return undefined
  const trafficString = decodeURIComponent(rawTrafficString)
  try {
    return JSON.parse(trafficString)
  } catch {
    return {raw: trafficString}
  }
}

export function makeClient(baseUrl: string | URL) {
  return {
    submitContactForm,
    registerEmail,
    getProductsAndMetadata,
    getPageTypeData,
    getManualDeals,
    trackRequest,
    getAllCategories,
    TEST_clear: () => {},
  }

  async function submitContactForm(data: ContactFormData): Promise<void> {
    return await fetchAsJsonWithJsonBody(buildUrl('./contact', baseUrl), data, {
      redirect: 'error',
    })
  }

  async function registerEmail(data: RegisterEmailData): Promise<{type: string}> {
    const dataForApi: SubscriptionOriginalApiDataModel = {
      email: data.email,
      app_id: data.appId,
      user_id: data.userId,
      subscription_location: data.subscriptionLocation,
    }
    return await fetchAsJsonWithJsonBody(buildUrl('./collect_email', baseUrl), {
      subscription: dataForApi,
    })
  }

  async function getProductsAndMetadata(
    search: string,
    userAgent?: string,
    {pageQueryParameters}: {pageQueryParameters: Record<string, string> | undefined} = {
      pageQueryParameters: undefined,
    },
  ): Promise<ProductsAndMetadataResponse> {
    const headers = userAgent ? {'user-agent': userAgent} : undefined

    const apiUrl = buildUrl('./products', baseUrl, {
      search,
      ...(pageQueryParameters ? pageQueryParameters : {}),
    })
    const {metadata, products}: ProductsAndMetadataOriginalApiResponse = await fetchAsJson(apiUrl, {
      headers,
    })

    // convert all keys to camelCase
    const normalizedProducts = camelcaseKeys(products)
    const normalizedMetadata = camelcaseKeys(metadata)

    return {
      metadata: normalizedMetadata,
      products: normalizedProducts.map((product) => {
        const randomClickId = randomNumber()
        return {
          ...product,
          redirectLinkTo: product.redirectLinkTo.replace(
            'rf_client_click_id=000000000',
            `rf_client_click_id=${randomClickId}`,
          ),
          clickId: randomClickId,
        }
      }),
      TEST_apiUrl: apiUrl.href,
    }
  }

  async function getPageTypeData(
    commonBdtParameters: CommonBdtParameters,
    search: string,
  ): Promise<PageTypeResponse> {
    const localedBaseUrl = modifyBaseUrlBasedOnHostHeader(baseUrl, commonBdtParameters.hostHeader)
    const slugAsText = slugToText(search, {capitalize: false})

    const {page_type, popular_categories, subcategories, banner}: PageTypeOriginalApiResponse =
      await fetchAsJson(buildUrl('./page_type', localedBaseUrl, {search: slugAsText}), {
        headers: generateCommonHeaders(commonBdtParameters, baseUrl.toString()),
      })

    const popularCategories = normalizePageTypeCategories(popular_categories || [])
    const normalizedSubcategories = normalizePageTypeCategories(subcategories || [])

    return {
      banner,
      pageType: page_type,
      popularCategories,
      subcategories: normalizedSubcategories,
    }
  }

  async function getManualDeals({
    location,
    positions,
    l1Taxonomy,
  }: ManualDealsParams): Promise<ManualDealsResponse> {
    const response: ManualDealsOriginalApiResponse = await fetchAsJson(
      buildUrl(
        './manual_deals',
        baseUrl,
        {
          location,
          positions,
          l1Taxonomy: l1Taxonomy ?? undefined,
        },
        {style: 'RoR'},
      ),
    )

    return {
      topDeals: getManualDealsByPositionOrUndefined(positions, 'Top Deals', response),
      topOffers: getManualDealsByPositionOrUndefined(positions, 'Top Offers', response),
      popups: getManualDealsByPositionOrUndefined(positions, 'Popups', response),
      banners: getManualDealsByPositionOrUndefined(positions, 'Banners', response),
      coupons: getManualDealsByPositionOrUndefined(positions, 'Coupons', response),
      offers: getManualDealsByPositionOrUndefined(positions, 'Offers', response),
    }
  }

  // This will only be used for tracking purposes now, config fetching is moved to new stack
  // Return values are still here, but they are only used in tests
  async function trackRequest(
    commonBdtParameters: CommonBdtParameters,
    cookieSetter?: CookieSetterFunction,
  ): Promise<TrackRequestApiResponse> {
    const {locale} = mapHostHeaderToLocaleParams(commonBdtParameters.hostHeader)
    const trackRequestResponse = await fetch(buildUrl(`./track_request`, baseUrl, {locale}), {
      headers: {
        'Content-Type': 'application/json',
        ...generateCommonHeaders(commonBdtParameters, baseUrl.toString()),
      },
      method: 'POST',
      body: JSON.stringify({ab_tests: commonBdtParameters.abTests}),
    })
    if (!trackRequestResponse.ok) await throwErrorFromBadStatus(trackRequestResponse)

    //@ts-expect-error https://github.com/node-fetch/node-fetch#extract-set-cookie-header
    const trackRequestCookies = trackRequestResponse.headers.raw()['set-cookie']

    const {bdtSession, traffic} = extractTrackingValuesFromCookies(trackRequestCookies)

    if (cookieSetter) {
      traffic && cookieSetter('traffic', decodeURIComponent(traffic))
      bdtSession && cookieSetter('_noam-stucco_session', decodeURIComponent(bdtSession))
    }

    return {
      traffic: extractTrafficFromCookieValue(traffic),
      trackRequestCookies,
    }
  }

  function getAllCategories(
    commonBdtParameters: CommonBdtParameters,
  ): Promise<CategoryOriginalApiModel[]> {
    const {locale} = mapHostHeaderToLocaleParams(commonBdtParameters.hostHeader)

    return fetchAsJson(buildUrl('./categories', baseUrl, {locale}), {
      headers: generateCommonHeaders(commonBdtParameters, baseUrl.toString()),
    })
  }
}

export function normalizePageTypeCategories(categories: CategoryOriginalApiModel[]): Category[] {
  return categories.map(({title, url, img, image}) => ({
    title,
    url,
    image: image || img,
  }))
}

export function normalizeManualDeal(manualDeal?: ManualDealOriginalApiModel): ManualDeal {
  const manualDealItems = manualDeal?.items.map((deal) => ({...deal, clickId: randomNumber()}))
  return {
    duration: manualDeal?.duration || 0,
    items: camelcaseKeys(manualDealItems || []),
  }
}

export function getManualDealsByPositionOrUndefined(
  positions: ManualDealsPositions,
  positionKey: ManualDealsPositions[number],
  response: ManualDealsOriginalApiResponse,
) {
  return positions.includes(positionKey) ? normalizeManualDeal(response[positionKey]) : undefined
}

function generateCommonHeaders(
  {userAgent, userId, sessionId, pageId, requestPath, bdtSession, traffic}: CommonBdtParameters,
  baseUrl: string,
): import('node-fetch').HeadersInit {
  return {
    ...(userAgent ? {'user-agent': userAgent} : {}),
    ...(requestPath ? {referer: new URL(requestPath, baseUrl).href} : {}),
    cookie: [
      userId ? `user_id=${userId}` : undefined,
      sessionId ? `page_group_id=${sessionId}` : undefined,
      pageId ? `page_load_id=${pageId}` : undefined,
      bdtSession ? `_noam-stucco_session=${bdtSession}` : undefined,
      traffic ? `traffic=${traffic}` : undefined,
    ]
      .filter((x) => !!x)
      .join('; '),
  }
}

function extractTrackingValuesFromCookies(cookies: Array<string>): TrackingCookies {
  const trafficCookieString = cookies.find((cookieString: string) =>
    cookieString.includes('traffic'),
  )
  const bdtSessionCookieString = cookies.find((cookieString: string) =>
    cookieString.includes('_noam-stucco_session'),
  )
  return {
    traffic: trafficCookieString ? extractValueFromCookieString(trafficCookieString) : undefined,
    bdtSession: bdtSessionCookieString
      ? extractValueFromCookieString(bdtSessionCookieString)
      : undefined,
  }
}

function extractValueFromCookieString(cookieString: string): string {
  return cookieString.split(';')[0].split('=')[1]
}
