import capitalize from 'lodash.capitalize'
import kebabCase from 'lodash.kebabcase'

/**
 *
 * @param {string} slug
 * @param {{capitalize?: boolean; urlDecode?: boolean}} [options]
 * @return {string}
 */
export function slugToText(slug, {capitalize: shouldCapitalize = true, urlDecode} = {}) {
  let result = urlDecode ? decodeURIComponent(slug).replaceAll('\n', ' ') : slug

  if (result.startsWith('/')) {
    result = result.slice(1)
  }

  result = result.replace(/_|-|%20/g, ' ')

  if (shouldCapitalize) {
    result = capitalize(result)
  }

  return result
}

/**
 * @param {string} text
 * @param {{urlDecode?: boolean}} [options]
 * @return {string}
 */
export function textToSlug(text, {urlDecode} = {}) {
  const input = urlDecode ? decodeURIComponent(text).replaceAll('\n', ' ') : text
  return kebabCase(input.toLowerCase().trim())
}
