import merge from 'lodash.merge'
import {makeError} from '@roundforest/functional-commons'
import {delay} from '@roundforest/promise-commons'

/**
 * @typedef {import('./node.index.js').RequestInitWithRequestId} RequestInitWithRequestId
 * @typedef {(key: string, value: any) => any} JsonReviver
 * @typedef {import('./node.index.js').Fetcher} nodeFetch
 * @typedef {import('./browser.index.js').Fetcher} BrowserFetch
 * */
/**
 * @param {BrowserFetch | nodeFetch} fetcher
 * @param {Symbol} [requestIdGetterSymbol]
 * */
export default function makeClient(fetcher, requestIdGetterSymbol) {
  /**
   * @param {import('node-fetch').Response | Response} response
   * @returns {Promise<void>}
   */
  async function throwErrorFromBadStatus(response) {
    const body = await response.text().catch(() => `failed to get body of error`)

    throw makeError(`Response ${response.status} returned from ${response.url}, body: ${body}`, {
      code: 'ERR_X_STATUS_CODE_NOT_OK',
      status: response.status,
      statusText: response.statusText,
      // @ts-expect-error
      headers: response?.headers?.raw?.() ?? response.headers,
      body: body,
    })
  }

  /**
   * @param {string | URL} url
   * @param {RequestInitWithRequestId} [init]
   * @param {JsonReviver=} jsonReviver
   * @returns {Promise<any>}
   */
  async function fetchAsJson(url, init, jsonReviver) {
    // @ts-expect-error
    const response = await fetcher(url, merge({headers: {Accept: 'application/json'}}, init))

    if (!response.ok) await throwErrorFromBadStatus(response)

    return jsonReviver
      ? await JSON.parse(await response.text(), jsonReviver)
      : await response.json()
  }

  /**
   * @param {string | URL} url
   * @param {RequestInitWithRequestId} [init]
   * @returns {Promise<string>}
   */
  async function fetchAsText(url, init) {
    // @ts-expect-error
    const response = await fetcher(url, init)

    if (!response.ok) await throwErrorFromBadStatus(response)

    return await response.text()
  }

  /**
   * @param {string | URL} url
   * @param {import('type-fest').JsonValue} json
   * @param {RequestInitWithRequestId} [init]
   * @returns {Promise<string>}
   */
  async function fetchAsTextWithJsonBody(url, json, init) {
    const response = await fetcher(
      url,
      // @ts-expect-error
      merge(
        {method: 'POST', headers: {'Content-Type': 'application/json'}, body: JSON.stringify(json)},
        init || {},
      ),
    )

    if (!response.ok) await throwErrorFromBadStatus(response)

    return await response.text()
  }

  /**
   *
   * @param {string | URL} url
   * @param {RequestInitWithRequestId} [init]
   * @returns {Promise<Buffer>}
   */
  async function fetchAsBuffer(url, init) {
    const response = await /** @type {nodeFetch}*/ (fetcher)(url, init)

    if (!response.ok) await throwErrorFromBadStatus(response)

    return await response.buffer()
  }
  /**
   * @param {string | URL} url
   * @param {import('type-fest').JsonValue} json
   * @param {RequestInitWithRequestId} [init]
   * @returns {Promise<Buffer>}
   */
  async function fetchAsBufferWithJsonBody(url, json, init) {
    const response = await /** @type {nodeFetch}*/ (fetcher)(
      url,
      merge(
        {method: 'POST', headers: {'Content-Type': 'application/json'}, body: JSON.stringify(json)},
        init || {},
      ),
    )

    if (!response.ok) await throwErrorFromBadStatus(response)

    return await response.buffer()
  }

  /**
   * @param {string | URL} url
   * @param {import('type-fest').JsonValue} json
   * @param {RequestInitWithRequestId} [init]
   * @param {JsonReviver=} jsonReviver
   * @returns {Promise<any>}
   */
  async function fetchAsJsonWithJsonBody(url, json, init, jsonReviver) {
    const response = await fetcher(
      url,
      // @ts-expect-error
      merge(
        {
          method: 'POST',
          headers: {'Content-Type': 'application/json', Accept: 'application/json'},
          body: JSON.stringify(json),
        },
        init || {},
      ),
    )

    if (!response.ok) await throwErrorFromBadStatus(response)

    return jsonReviver ? JSON.parse(await response.text(), jsonReviver) : await response.json()
  }

  /**
   * @param {Function} f
   * @param {{retries?: number, sleepTime?: number, backoff?: number, idempotent?: boolean}} options
   */
  async function retryFetch(
    f,
    {retries = 2, sleepTime = 100, backoff = 1.1, idempotent = true} = {},
  ) {
    for (let retry = 0; retry <= retries; retry++) {
      if (retry === retries) {
        return await f(retry)
      }
      try {
        return await f(retry)
      } catch (/**@type {any}*/ err) {
        if (CONNECTION_ERROR_CODES.has(err.code) || CONNECTION_ERROR_CODES.has(err.type)) {
          // retry
        } else if (err.code === 'ERR_X_STATUS_CODE_NOT_OK') {
          if (err.status >= 300 && err.status < 500) {
            throw err
          }
        } else if (idempotent) {
          // retry
        } else {
          throw err
        }
        await delay(sleepTime * backoff ** retry)
      }
    }
  }

  /**
   * @param {() => string | undefined} requestIdGetter
   */
  function setGlobalRequestIdGetter(requestIdGetter) {
    // @ts-expect-error
    if (globalThis[requestIdGetterSymbol] != null) {
      throw new Error('you are not allowed to change global request id getter')
    }
    // @ts-expect-error
    globalThis[requestIdGetterSymbol] = requestIdGetter
  }

  // /**@type {(() => string | undefined) | undefined} */
  // // @ts-expect-error
  // globalThis[requestIdGetterSymbol] = undefined

  const CONNECTION_ERROR_CODES = new Set([
    'ENOTFOUND',
    'ECONNREFUSED',
    'ETIMEDOUT',
    'EHOSTUNREACH',
    'ECONNRESET',
    'request-timeout',
  ])

  return {
    throwErrorFromBadStatus,
    fetchAsJson,
    fetchAsText,
    fetchAsTextWithJsonBody,
    fetchAsBuffer,
    fetchAsBufferWithJsonBody,
    fetchAsJsonWithJsonBody,
    setGlobalRequestIdGetter,
    retryFetch,
    fetch: fetcher,
  }
}
