/**
 *
 * @param {URL | string} url
 * @param {string} queryParam
 * @param {string | number | boolean | string[] | number[] | boolean[]} [value]
 * @param {{style: 'RoR'}} [options]
 *
 * @returns {URL}
 */
export function addQueryParamToUrl(url, queryParam, value, options) {
  if (typeof url === 'string') {
    url = new URL(url)
  }
  if (value != null) {
    if (Array.isArray(value)) {
      value.forEach((valueItem) => {
        const queryParamKey = options?.style === 'RoR' ? `${queryParam}[]` : queryParam
        // @ts-expect-error (because set _does_ allow numbers, which it converts to string)
        return /** @type {URL} */ (url).searchParams.append(queryParamKey, valueItem)
      })
    } else {
      // @ts-expect-error (because set _does_ allow numbers, which it converts to string)
      url.searchParams.set(queryParam, value)
    }
  }

  return url
}

/**
 * @param {string | URL} url
 * @param {|
 * 'protocol' |
 * 'username' |
 * 'password' |
 * 'host' |
 * 'hostname' |
 * 'port' |
 * 'pathname' |
 * 'search' |
 * 'hash'
 * } field
 * @param {(string | undefined)=} value
 */
export function overrideUrlField(url, field, value) {
  const urlUrl = typeof url === 'string' ? new URL(url) : url

  if (value === undefined) return urlUrl

  urlUrl[field] = value

  return urlUrl
}

/**
 *
 * @param {URL | string} url
 * @param {Record<string, string | number | boolean | string[] | number[] | boolean[] | undefined>} queryParams
 * @param {{style: 'RoR'}} [options]
 *
 * @returns {URL}
 */
export function addQueryParamsToUrl(url, queryParams, options) {
  if (typeof url === 'string') {
    url = new URL(url)
  }

  for (const [queryParam, value] of Object.entries(queryParams)) {
    if (value != null) {
      if (Array.isArray(value)) {
        value.forEach((valueItem) => {
          const queryParamKey = options?.style === 'RoR' ? `${queryParam}[]` : queryParam
          // @ts-expect-error (because set _does_ allow numbers, which it converts to string)
          return /** @type {URL} */ (url).searchParams.append(queryParamKey, valueItem)
        })
      } else {
        // @ts-expect-error (because set _does_ allow numbers, which it converts to string)
        url.searchParams.set(queryParam, value)
      }
    }
  }

  return url
}

/**
 *
 * @param {URL | string} url
 * @param {URL | string | undefined} baseUrl
 * @param {Record<string, string|number|boolean|string[]|number[]|boolean[]|undefined>} [queryParams]
 * @param {{style: 'RoR'}} [options]
 *
 * @returns {URL}
 */
export function buildUrl(url, baseUrl, queryParams, options) {
  if (baseUrl != null && typeof baseUrl === 'string') {
    baseUrl = new URL(baseUrl)
  }

  const finalUrl = new URL(url, baseUrl)

  return queryParams ? addQueryParamsToUrl(finalUrl, queryParams, options) : finalUrl
}

/**
 *
 * @param {string} pathSegment
 * @param {string} pathParam
 * @param {string|undefined} value
 *
 * @returns {string}
 */
export function addPathParamToPathSegment(pathSegment, pathParam, value) {
  const pathParams = parsePathSegment(pathSegment)

  pathParams[pathParam] = value

  return buildPathSegmentFromPathParams(pathParams)
}

/**
 *
 * @param {string} pathSegment
 * @returns {Record<string, string|undefined>}
 */
export function parsePathSegment(pathSegment) {
  /**@type {Record<string, string>} */
  const ret = Object.create(null)

  if (pathSegment === '') {
    return ret
  }

  pathSegment.split('&').forEach((param) => {
    const [name, value, ...rest] = param.split('=')

    if (name === '__proto__') {
      throw new Error(
        `someone is trying to hack us via proto poisoning with path segment ${pathSegment}`,
      )
    }

    ret[name] = rest.length > 0 ? [value, ...rest].join('=') : value
  })

  return ret
}

/**
 *
 * @param {Record<string, string|undefined>} params
 *
 * @returns {string}
 */
export function buildPathSegmentFromPathParams(params) {
  return Object.entries(params)
    .map(([name, value]) => {
      if (name.includes('&') || name.includes('=')) {
        throw new Error('"pathParam must not include parth param separators "&" and "="')
      }

      if (value && value.includes('&')) {
        throw new Error('"value must not include parth param separator "="')
      }

      return `${name}${value == null ? '' : '='}${value == null ? '' : value}`
    })
    .join('&')
}
