import {mapHostHeaderToLocaleParams} from '@roundforest/bdt-locale-commons'

export function modifyBaseUrlBasedOnHostHeader(
  baseUrl: string | URL,
  hostHeader: string | undefined,
) {
  const {locale} = mapHostHeaderToLocaleParams(hostHeader)

  const localedBaseUrl = new URL(baseUrl)
  const hostnameSegments = localedBaseUrl.hostname.split('.')

  if (hostnameSegments[1] !== 'bestdeals') return baseUrl

  const normalizedLocale = locale === 'en' ? 'www' : locale
  localedBaseUrl.hostname = [normalizedLocale, ...hostnameSegments.slice(1)].join('.')

  return localedBaseUrl.href
}
