/** Referenced with thanks from https://github.com/jackniu81/node-unslug#readme under MIT licence */

const SMALL_WORDS =
  /\b(?:an?d?|a[st]|because|but|by|en|for|i[fns]|neither|nor|o[fnr]|only|over|per|so|some|tha[tn]|the|to|up|upon|vs?\.?|versus|via|when|with|without|yet)\b/i

const SMALL_ACCENTS = /^[àâäèéêëîïôœùûüÿç]$/i
const SPACE_TOKENS = /[\s:–—-]+|./g

const splitByTokensRules = {
  'all-in-number': /[a-z0-9]+-in-\d+/, // ex: all-in-1, 5-in-1
  'letter-word': /^[a-z]-[a-z]+/, // ex: E-reader, k-cup
  'non-word': /^non-[a-z]+/, // ex: non-smart, non-slip
  'number-number': /\d+-\d+/, // ex: 5-1 converted,
  'number-letters': /\d+-[a-zA-Z]+/, // 4-way switch
  'split-dashes': /[^\s:–—-]+/,
}

const SPLIT_BY_TOKENS = new RegExp(
  `(${Object.values(splitByTokensRules)
    .map((x) => x.source)
    .join('|')}|.)`,
  'g',
)

const FIRST_LETTER_AFTER_NUMERIC_PREFIX = /-[a-z]/i
const HAS_NUMERIC_PREFIX = /\d+-/

const ALPHANUMERIC_PATTERN = /[A-Za-z0-9\u00C0-\u00FF]/

const UNITS = /(lb|oz|yd|ft|cm|mm|hz|w)$/i
const FULL_WORD_UNITS = /(watt|volt|foot)$/i
const INCH = /(\d-inch|\d+in)$/i
const MIXED_LETTERS_AND_NUMBERS = /(([a-zA-Z]+[0-9]+)|([0-9]+[a-zA-Z]+))/

const KNOWN_WORDS = new Map([
  ['lg', 'LG'],
  ['hp', 'HP'],
  ['oled', 'OLED'],
  ['led', 'LED'],
  ['uhd', 'UHD'],
  ['fhd', 'FHD'],
  ['hd', 'HD'],
  ['tv', 'TV'],
  ['tvs', 'TVs'],
  ['ac', 'AC'],
  ['pc', 'PC'],
  ['ac', 'AC'],
  ['av', 'AV'],
  ['rc', 'RC'],
  ['dvr', 'DVR'],
  ['vcr', 'VCR'],
  ['tcl', 'TCL'],
  ['jbl', 'JBL'],
  ['ptz', 'PTZ'],
  ['gps', 'GPS'],
  ['uv', 'UV'],
  ['nas', 'NAS'],
  ['rv', 'RV'],
  ['hdtv', 'HDTV'],
  ['suv', 'SUV'],
  ['psi', 'PSI'],
  ['hdmi', 'HDMI'],
  ['gopro', 'GoPro'],
  ['petg', 'PETG'],
  ['anr', 'ANR'],
  ['lipo', 'LiPo'],
  ['iphone', 'iPhone'],
  ['ipad', 'iPad'],
  ['ipod', 'iPod'],
  ['imac', 'iMac'],
  ['wifi', 'WiFi'],
  ['iphones', 'iPhones'],
  ['ipads', 'iPads'],
  ['ipods', 'iPods'],
  ['imacs', 'iMacs'],
  ['dslr', 'DSLR'],
  ['ev', 'EV'],
  ['pcs', 'PCs'],
  ['dvd', 'DVD'],
  ['dvds', 'DVDs'],
])

/**
 * @param {{
 * 0: string,
 * index: number
 * input: string
 * }} token
 * */
function handleToken({0: token, index, input}) {
  if (!SPACE_TOKENS.test(token)) {
    return ' '
  }

  const bigWord = KNOWN_WORDS.get(token)
  if (bigWord) {
    return bigWord
  }

  if (splitByTokensRules['letter-word'].test(token)) {
    return token.replace(/[a-z]-[a-z]/, (m) => m.toUpperCase())
  }

  if (splitByTokensRules['non-word'].test(token)) {
    return token.replace(token[0], token[0].toUpperCase()).replace(/-[a-z]/, (m) => m.toUpperCase())
  }

  if (MIXED_LETTERS_AND_NUMBERS.test(token)) {
    if (UNITS.test(token)) {
      return token.toLowerCase()
    }
    if (INCH.test(token)) {
      return token.replace(/in/i, '"')
    }

    return token.toUpperCase()
  }

  if (SMALL_ACCENTS.test(token)) {
    return token.toLowerCase()
  }

  if (HAS_NUMERIC_PREFIX.test(token)) {
    if (splitByTokensRules['all-in-number'].test(token)) {
      return token.toLowerCase()
    }
    if (INCH.test(token)) {
      return token.replace(/-inch/i, '"')
    }

    if (FULL_WORD_UNITS.test(token)) {
      return token.replace(/-[a-z]/, (m) => m.toUpperCase().replace('-', ' '))
    }

    if (UNITS.test(token)) {
      return token.replace('-', '').toLowerCase()
    }

    return token.replace(FIRST_LETTER_AFTER_NUMERIC_PREFIX, (m) => m.toUpperCase())
  }

  if (!SMALL_WORDS.test(token) || index === 0 || index + token.length === input.length) {
    // Find and uppercase first word character, skips over *modifiers*.
    return token.replace(ALPHANUMERIC_PATTERN, (m) => m.toUpperCase())
  }

  return token
}

/** @param {string} input */
export function unslug(input) {
  // These two lines are a HACK! We are _relying_ on the state of SPACE_TOKENS that is used by "/g"
  // to make it work (it is a global regex). So instead of making the algorithm not rely
  // on the global regex state, we just reset the global state for each unslug.
  // What we should have done is fix the algorithm to not rely on the global regex state.
  // See also: https://stackoverflow.com/questions/1520800/why-does-a-regexp-with-global-flag-give-wrong-results
  SPACE_TOKENS.lastIndex = 0
  SPLIT_BY_TOKENS.lastIndex = 0
  try {
    const titleCase = Array.from(input.matchAll(SPLIT_BY_TOKENS))
      .map((token) => handleToken({0: token[0], index: /** @type {number} */ (token.index), input}))
      .join('')

    return titleCase
  } catch (error) {
    throw new Error(`cannot unslug: ${input}, only string inputs are allowed`)
  }
}
